import { FC, useEffect, useRef, useState } from 'react'
import { BsCart, BsTrash } from 'react-icons/bs'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import CartItem from '../../components/CartItem/CartItem'
import ModalWindow from '../../components/ui/ModalWindow/ModalWindow'
import { selectCart } from '../../redux/cart/selectors'
import { calcTotalCount } from '../../utils/calcTotalCount'
import styles from './Cart.module.scss'
import CartEmpty from './CartEmpty/CartEmpty'

const Cart: FC = () => {
	const { totalPrice, items } = useSelector(selectCart)
	const totalCount = calcTotalCount(items)
	const isMounted = useRef(false)//
	
const [isModalVisible, setIsModalVisible] = useState<boolean>(false)

	useEffect(() => {
		if (isMounted.current) {
			const json = JSON.stringify(items)
			localStorage.setItem('cart', json)
		}
		isMounted.current = true
	}, [items])


	if (items.length < 1) {
			return <CartEmpty /> 
	}
	
		return (
			<div className={styles.cart_container}>
					<div className={styles.cart__top}>
						<h2>
							<BsCart />
							Кошик
						</h2>
						<div className={styles.cart__clear} onClick={() => setIsModalVisible(true)}>
							<BsTrash />
							<span>ОЧИСТИТИ КОШИК</span>
						</div>
						{isModalVisible && <ModalWindow actionToConfirm = 'clearCart' setIsModalVisible={setIsModalVisible}/>}
					</div>
						{items.map((item: any) => (
							<CartItem {...item} key={item.id} />
						))}
					<div className={styles.cart__bottom}>
						<div className={styles.cart__bottom_details}>
							<span>
								Всього піц: <b>{totalCount} шт.</b>
							</span>
							<span>
								Сума замовлення: <b>{totalPrice} грн</b>
							</span>
						</div>
						<div className={styles.cart__bottom_buttons}>
							<Link
								to='/'
								className='button button--outline go-back-btn'
							>
								<span>Назад</span>
							</Link>
							<Link to='/payment' className='button pay-btn'>
								<span>ЗАМОВИТИ</span>
							</Link>
						</div>
					</div>
			</div>
		)
}

export default Cart
